* {
  box-sizing: border-box;
}

@font-face {
  font-family: 'Verdana';
  src: url('../public/fonts/Verdana.eot');
  src: url('../public/fonts/Verdana.eot?#iefix') format('embedded-opentype'),
    url('../public/fonts/Verdana.woff2') format('woff2'),
    url('../public/fonts/Verdana.woff') format('woff'),
    url('../public/fonts/Verdana.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Verdana', sans-serif;
  background: #fdfdfd;
}

a {
  color: black;
}

a {
  pointer-events: all;
  color: black;
  text-decoration: none;
}

svg {
  fill: black;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

canvas {
  opacity: 0;
  touch-action: none;
  animation: fade-in 5s ease 1s forwards;
}

.nsh-intro-wrap {
  width: 100vw;
  height: 100vh
}

.nsh-intro-popup-title {
  font-family: verdana;
  color: #636363;
  font-size: 18px;
  margin-top: 10px;
}

.nsh-intro-popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
  z-index: 100;
  min-width: 300px;
}

.nsh-intro-popup-sep {
  display: block;
  width: 80%;
  background: #a7a7a7;
  height: 1px;
  margin: 10px auto 20px auto;
}

.nsh-intro-popup-button {
  background: #606060;
  color: #fff;
  border: 0px;
  padding: 8px 20px;
  font-size: 18px;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
}

.nsh-intro-popup-overlay {
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  width: 100%;
  z-index: 2;
}

.nsh-intro-popup-button-wrap {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.nsh-intro-progress {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
}


/* General styling for the slider */
input[type="range"] {
  -webkit-appearance: none;
  /* Remove default styles for WebKit browsers */
  appearance: none;
  /* Remove default styles for modern browsers */
  width: 100%;
  /* Full width */
  height: 12px;
  /* Height of the track */
  background: #ddd;
  /* Background color for the track */
  border-radius: 0px;
  /* Rounded corners for the track */
  outline: none;
  /* Remove focus outline */
  opacity: 0.7;
  /* Make the track slightly transparent */
  transition: opacity 0.2s;
}

.nsh-intro-progress:hover {
  opacity: 1;
  /* Full opacity on hover */
}

/* Customize the thumb */
.nsh-intro-progress::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* Remove default styles for WebKit browsers */
  appearance: none;
  /* Remove default styles for modern browsers */
  width: 10px;
  height: 20px;
  background: linear-gradient(to bottom, #141414, #636363);
  cursor: pointer;
  border: 0px;
  border-radius: 0px;
  transform: translateY(-6px);
  /* Border for better visibility */
}

/* Customize the thumb for other browsers */
.nsh-intro-progress::-moz-range-thumb {
  width: 10px;
  height: 20px;
  background: linear-gradient(to bottom, #141414, #636363);
  cursor: pointer;
  border: 0px;
  border-radius: 0px;
}

.nsh-intro-progress::-ms-thumb {
  width: 10px;
  height: 20px;
  background: linear-gradient(to bottom, #141414, #636363);
  cursor: pointer;
  border: 0px;
  border-radius: 0px;
}

/* Style the filled portion of the track (progress bar) */
.nsh-intro-progress::-webkit-slider-runnable-track {
  background: #39f;
  height: 8px;
  border-radius: 0px;

}

.nsh-intro-progress::-moz-range-progress {
  background: #39f;
  height: 12px;
}

.nsh-intro-progress::-ms-fill-lower {
  background: #39f;
  height: 8px;
}

.nsh-intro-progress::-ms-fill-upper {
  background: #ddd;
  height: 8px;
}

/* Customize the track for different states */
.nsh-intro-progress:focus::-webkit-slider-runnable-track {
  background: linear-gradient(to right, #39f 0%, #39f 50%, #39f 50%, #39f 100%);
}

/* Internet Explorer/Edge-specific adjustments */
.nsh-intro-progress::-ms-track {
  width: 100%;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}


/****************************************************
 *  Chamber Upload Button
 ****************************************************/

.nsh-upload-button {
  background: #007818;
  color: white;
  border: 0px;
  padding: 10px 20px;
}

.nsh-upload-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
}

.nsh-upload-label {
  font-size: 18px;
  margin-right: 10px;
  text-align: center;
  width: 100%;
}

.nsh-uploaded-img {
  box-shadow: 25px 25px 50px 0 rgba(255, 255, 255, 0.5) inset, -25px -25px 50px 0 rgba(255, 255, 255, 0.5) inset;
  width: 350px;
  height: 300px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
}

.nsh-file-input {
  display: none;
}

.nsh-upload-form-image,
.nsh-upload-form-voice {
  display: flex;
  height: 300px;
  width: 300px;
  padding: 80px 40px;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  gap: 20px;
  background: rgb(255, 255, 255);
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3) 0%, rgba(148, 187, 233, 0) 100%);
}

.nsh-upload-wrap {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.nsh-control-toggle {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.nsh-control-panel {
  display: flex;
  gap: 20px;
  justify-content: center;
  position: fixed;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  width: 100%;
  padding: 20px;
}

.nsh-control-panel.nsh-bottom-left-control.nsh-on-screen-control {
  width: 150px;
  right: 50px;
  bottom: 40px;
  padding: 15px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto auto auto;
  border-radius: 15px;
  gap: 8px;
}

.nsh-control-panel button {
  cursor: pointer;
}

.nsh-control-panel.nsh-bottom-left-control.nsh-on-screen-control button {
  background: transparent;
  box-shadow: unset;
  border: none;

}

.nsh-control-panel.nsh-bottom-left-control.nsh-on-screen-control button:first-child,
.nsh-control-panel.nsh-bottom-left-control.nsh-on-screen-control button:last-child {
  grid-column: span 2;
}

.nsh-control-panel.nsh-bottom-left-control.nsh-on-screen-control button svg {
  fill: #fff;
  border: 1px solid #fff;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  padding: 4px;
}

.nsh-control-panel.nsh-top-right-control {
  width: 175px;
  height: 60px;
  right: 50px;
  top: 40px;
  background: transparent;
  padding: 0;
  align-items: center;
}

.nsh-control-panel__inner.nsh-zoom-control,
.nsh-control-panel__inner.nsh-audio-control,
.nsh-control-panel__inner.nsh-instruction-control {
  background: rgba(0, 0, 0, 0.4);
  padding: 13px;
  border-radius: 15px;
  gap: 10px;
  display: flex;
  height: 60px;
}

.nsh-control-panel__inner.nsh-zoom-control button,
.nsh-control-panel__inner.nsh-audio-control button,
.nsh-control-panel__inner.nsh-instruction-control button {
  background: #fff;
  border-color: transparent;
  border-radius: 50%;
  padding: 5px 13px;
}

.nsh-control-panel__inner.nsh-audio-control button,
.nsh-control-panel__inner.nsh-instruction-control button {
  border-radius: 50%;
  border: none;
  padding: 8px 10px 5px 10px;
}

.nsh-control-panel.nsh-instructions {
  width: 250px;
  bottom: 220px;
  right: 50px;
  flex-direction: column;
  color: #fff;
  gap: 0;
  border-radius: 15px;
}

.nsh-control-panel.nsh-instructions h4 {
  margin: 0;
  font-size: 14px;
}

.nsh-control-panel.nsh-instructions ul {
  padding: 0;
  list-style: none;
}

.nsh-control-panel.nsh-instructions svg {
  fill: #fff;
  font-size: 18px;
}

.nsh-control-panel.nsh-instructions ul li {
  font-size: 13px;
  margin-bottom: 10px;
}

.nsh-control-panel.nsh-instructions p {
  margin: 0 0 10px 0;
  font-size: 14px;
  text-align: center;
}